.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {

}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Start of Anchor styling */

:root {
  --orange_text: #ff8c00;
  --highlight_color: #61dafb;
  --accent1: #2e8b57;
  --accent2: #2574A9;
  --base-gray: rgb(31,31,31) ;
  --mid-gray: rgb(40,40,40) ;
  --top-gray: rgb(54,54,54) ;
  --accent3: #006400;
  --font-gray: #8C979A;
  --login-welcome: rgb(45,204,116);
  --login-form: rgb(40,40,40);
  --button: rgb(52,180,135);
  --button-hover: rgb(36, 150, 110);

}

.invisible {
  display: none;
}

.visible {
  background-color: transparent;
}

/* General Page Styling */
.page-config {
  min-height: 100vh;
  height: 100%;
  display: flex;
}
.flex-spacer-4 {
  display: flex;
  flex-grow: 4;
  align-content: flex-start;
}
.item-center {
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;
}
.highlight:hover {
  color: var(--accent1);
}
.btn-text {
  background: none;
  padding: 4px 8px 0px 8px;
  margin: 0px 5px 0px 5px;
  color: var(--button);
  outline: none;
  border: none;
  font-size: 14px;
  cursor: pointer;
}
.btn-text:hover {
  color: var(--button-hover)
}
.btn-sm {
padding: 4px 8px 4px 8px;
margin: 5px;
background-color: var(--button);
border-radius: 10px;
color: white;
outline: none;
border: none;
cursor: pointer;
font-size: 14px;
}
.btn-sm:hover {
  background-color: var(--button-hover);
}

.btn-md {

}
.btn-lg {

}
/* Login Styling */
.main-login {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: var(--base-gray);
}
