.login-panel {
display: flex;
flex-direction: column;
width: 420px;
height: 640px;
background: var(--mid-gray);
color: white;
align-self: center;
border-radius: 15px;
}
.login-element {
  flex-grow: 2;
}
.form-inner {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
/* NavBar styling */
.navbar-start {
  position: fixed;
}
.navbar-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  background-color: var(--mid-gray);
  box-shadow: 0px 0.5px 0.5px rgba(0,0,0,0.5);
  z-index: 3;
  transition: all .7s ease-in;
}
.scrolled {
  position: fixed;
  top: 0;
  left: 0;
  background-color: var(--accent1);
}

.navbar-page {
  position: relative;
  padding: 10px 15px;
  font-size: 18px;
  text-decoration: none;
  color: white;
}


.navbar-hello {
  display: flex;
  position: relative;
  font-size: 18px;
  padding: 0px 0px 0px 6px;
  color: white;
}

.navbar-page:hover {
  color: var(--accent1);
}

.navbar-user-icon {
  position: relative;
  display: flex;
  align-self: flex-start;
  color: var(--orange_text);
  font-size: 20px;
  background-color: lightblue;
  padding: 5px;
  border-radius: 20px;
}

.logout-btn {
  position: relative;
  background-color: var(--top-gray);
  border: none;
  border-radius: 8px;
  position: relative;
  font-size: 16px;
  text-decoration: none;
  color: white;
  padding: 5px 8px 5px;
  margin: 0px 10px 0px 10px;
}

.logout-btn:hover {
  color: var(--accent1);
}

/* Popup Styling */
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0,0,0,0.2);
}
.popup-inner  {
  position: relative;
  padding: 20px 10px 20px 20px;
  width: 100%;
  max-width: 370px;
  height: 420px;
  background-color: var(--mid-gray);
  color: white;
  border-radius: 20px;

}
.popup-close {
  margin-top: 10px;
}

/* Side Bar styling */

.left-sidebar {
  position: relative;
  width: 15%;
  min-height: 100vh;
  min-width: 210px; 
  left: 0px;
  background-color: var(--mid-gray);
  float: left;
  box-shadow: 0.5px 0px 1px rgba(0,0,0,0.2);
  z-index: 2;
}
.left-sb-logo {
  font-size: 24px;
  padding: 5px 10px 10px 5px;
  background-color: var(--accent1);
  color: white;
  box-shadow: 0.5px 0px 5px rgba(0,0,0,0.6);
  /* border-bottom: solid var(--accent3) 0.5px; */
  margin-bottom: 25px;

}
.left-sb-navigation {
  display: flex;
  flex-direction: column;
  padding: 0px 0px 0px 0px;
  margin: 0;
  list-style-type: none;
  position: relative;
}
.left-sb-element {
  display: flex;
  position: relative;
  align-items:flex-start;
  font-size: 18px;
  margin: 0px;
  color: white;
  /* margin: 5px 10px 25px 10px; */
  padding: 10px;
  cursor: pointer;
}

.left-sb-element::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  background-color: var(--accent1);
  transition: border-radius 1ms ease;
  transition: transform 300ms ease-in-out;
  transform: scaleX(0);
  transform-origin: left;
}

.left-sb-element:hover::before,
.left-sb-element:focus::before {
  transform: scaleX(1);
}

/* Buckets Styling */
.bucket-container {
  margin-top: 3rem;
}
.bucket-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.buckets {
position: relative;
}

.buckets-title {
display: flex;
background-color: var(--accent1);
}

.buckets-header {
  display: flex;
  justify-content: flex-start;
  padding: 0rem 0rem 0rem 0.5rem;
  font-size: 22px;
  color: white;
  cursor: default;
}

.bucket-item-edit {
  display: flex;
  justify-content: flex-start;
  outline: none;
  color: white;
  border: none;
  border-bottom: solid 2px var(--top-gray);
  flex-grow: 4;
}

.bucket-item {
  color: white;
  cursor: pointer;
  white-space: nowrap;
}
.bucket-item:hover {
  color: var(--accent1);
}
.buckets-add {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 0.5rem;
  margin-top: auto;
  margin-bottom: auto;
  color: white;
  cursor: pointer;
}
.buckets-add:hover {
  opacity: 0.5;
}

.buckets-popup {
  width: 500px;
  height: 500px;
}
.bucket-list-sizing{
  overflow-y: scroll;
  overflow-x: hidden;
  height: 325px ;
}

.bucket-list-pop {
 display: flex;
 flex-direction: column;
 padding: 0;
 margin-right: 3px;
}
.bucket-elements-pop {
  display: flex;
  flex-direction: column;
}
.bucket-element-pop {
  display: flex;
  flex-direction: row;
  margin: 11px 0px 11px 0px;
  min-height: 22px;
}
.bucket-item-pop {
  display: flex;
  flex-grow: 1;
  justify-content: flex-start;

}
.bucket-icons-pop {
  display: flex;
  flex-grow: 4;
  justify-content: flex-end;
  align-items: center;
}

.bucket-add-pop {
display: flex;
}

.selected {
  color: var(--accent1);
}

/* Main Styling */

.main {
  background-color: var(--base-gray);
  float: right;
  min-height: 100vh;
  /* height: 100%; */
  width: 85%;
}

.add-task-container {
  position: relative;
  background-color: transparent;
  border: none;
  color: white;
  font-size: 14px;
  width: 120px;
  height: 30px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.add-task-btn {
  position: relative;
  background-color: transparent;
  border: none;
  color: white;
  font-size: 14px;
  padding: 10px 2px 10px 2px;
  text-align: center;
}

.add-task-angle {
  /* padding: 10px 2px 10px 2px; */
  transform: translateY(2px);
  color: white;
  font-size: 14px;
}


.task-editor-area {
  margin: 20px 10px 10px 10px;
  padding: 10px 5px 5px 5px; ;

}
.textarea-container1 {
  border: none;
  outline: none;
  border: solid 0.5px var(--top-gray);
  border-radius: 5px;
  background-color: var(--mid-gray);
  color: white;
  width: 800px;
  height: 150px;
  margin: auto;
}
.textarea-container-submit {
  margin-top: 5px;
  position: relative;
}

  .task-area1 {
  background: transparent;
  outline: none;
  resize: none;
  color: white;
  border: none;
  border-bottom: solid 0.5px var(--top-gray) ;
  margin-top: 5px;
  font-size: 16px;
  }
  .task-area2 {
    height: 100px;
    padding: 2px;
    margin: 2px 12px 2px 16px;
    overflow: auto;
    resize: both;
    -moz-appearance: textfield-multiline;
    -webkit-appearance: textarea;
    background: transparent;
    outline: none;
    resize: none;
    color: white;
    border: none;
    font-size: 16px;
    cursor: text;
    font-family: monospace;
    text-align: left;
    }

  .task-area2:empty::before {
    color: rgb(117,117,117);
    content: 'Task Details...';
  }

  .hover {
    color: var(--highlight_color);
  }
  

  /* Task Styling */
.fade-out{
  width: 5px;
  opacity: 0.3;
  background: rgba(31,31,31, 0.3);
}
/* Figure out how to make the overflow fadeout cleanly */

.task-edit {
  color: white;
}
.task-edit:hover {
  color: var(--accent1);
}

.task-item {
  display: flex;
  flex-direction: column;
  margin: 25px;
  padding: 5px;
  border: solid 0.5px var(--top-gray);
  color: white;
  min-height: 50px;
}
.task-element {
  margin: 0px 0px 0px 10px;
  align-self: flex-start;
  text-align: left;
}
.task-header{
  display: flex;
  flex-direction: row;
}
.task-title{
  margin-right: auto;
  overflow-x: hidden;
  cursor: pointer;
}
.task-body {
  display: flex;
  flex-direction: column;
}
.task-details {
  white-space: pre-wrap;
}

.task-icons {
margin: 4px;
font-size: 1rem;
display: flex;
white-space: nowrap;
}
.invisible-icons {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  transition: 400ms ease-in-out;
  transform: scaleX(0);
  transform-origin: right;
}
.task-icons-hover {
  z-index: 1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: 400ms ease-in-out;
  transform: scaleX(1);
  transform-origin: right;
}
.task-icon {
  margin-left: 4px;
  margin-right:  4px;
  font-size: 1rem;
}

.task-buckets {
display: flex;
flex-direction: row;
}

.task-bucket {
  margin: 10px 10px 10px 10px;
  padding: 3px 5px 3px 5px;
  border: solid 1px var(--top-gray);
  border-radius: 5px;
  cursor: pointer;
  color: var(--font-gray);
  opacity: 0.8;
}
.task-bucket:hover {
  border: solid 1px var(--mid-gray);
  background-color: var(--top-gray);
  color: white;
}
  .task-delete {
    color: white;
  }
  .task-delete:hover {
    color: var(--accent1);
  }


  /* Task List Styling */

  .task-list {
    display: flex;
    flex-direction: column;
  }

  /* Dropdown Styling */

  .dropdown-content {
    position: absolute;
    z-index: 3;
    background-color: var(--mid-gray);
    opacity: 0.9;
  }

  .dropdown-content-items{
    color: white;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }

  .dropdown-content-items:hover{
    color: var(--accent1);
    cursor: pointer;
  }